
.high-light-block {
  position: relative;
  display: inline-block;
  width: 100%;
  line-height: 1;
  margin-bottom: 10px;
  background: #3f3f3f;
  text-align: left;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
.high-light-block * {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
.high-light-block:hover .block-header .header-tool {
  display: inline-block;
}
.high-light-block.full-screen {
  height: 100vh;
  width: 100vw;
}
.high-light-block .block-header {
  display: inline-block;
  width: 100%;
  line-height: 1;
  padding: 5px 0;
  background: #3f3f3f;
  /*border-bottom: 1px solid #c5c5c5;*/
}
.high-light-block .block-header .header-title {
  float: left;
  display: inline-block;
  color: #ccc;
  text-align: right;
  padding: 5px 10px;
  font-weight: 600;
}
.high-light-block .block-header .header-lang {
  float: right;
  display: inline-block;
  color: #ccc;
  text-align: right;
  font-size: 20px;
  padding: 0 10px;
  font-weight: 600;
}
.high-light-block .block-header .header-tool {
  display: none;
  white-space: normal;
  position: absolute;
  top: 0;
  right: 0;
  z-index: 1;
  line-height: 1;
  /*background-color: #3C3F41;*/
  background-color: #2B2B2B;
  padding: 5px 10px;
}
.high-light-block .block-header .header-tool .tool-btn {
  display: inline-block;
  line-height: normal;
}
.high-light-block .block-header .header-tool .tool-btn .tool-icon {
  font-size: 20px;
  color: #9F9F9F;
  padding: 5px;
}
.high-light-block .block-header .header-tool .tool-btn .tool-icon:hover {
  color: #FFF;
}
.high-light-block .block-body {
  display: inline-block;
  width: 100%;
  line-height: 1;
  height: calc(100% - 32px);
  overflow: auto;
}
.high-light-block .block-body .line-block {
  float: left;
  width: 50px;
}
.high-light-block .block-body .line-block .line-num {
  display: inline-block;
  color: #5E6164;
  text-align: right;
  font-size: 12px;
  width: 100%;
  height: 22px;
  line-height: 22px;
  vertical-align: middle;
}
.high-light-block .block-body .line-block .line-num:after {
  content: ' ';
  display: inline-block;
  width: 1px;
  height: 100%;
  line-height: 2;
  border-right: 1px solid #c5c5c5;
  margin-left: 10px;
  vertical-align: middle;
}
.high-light-block .block-body .code-block {
  float: left;
  display: inline-block;
  width: 100%;
}
.high-light-block .block-body .code-block.code-block-calc {
  width: calc(100% - 50px);
}
.high-light-block .block-body .code-block .code-content {
  white-space: pre-wrap;
  word-wrap: break-word;
}
.high-light-block .block-body .code-block .code-content .line-code {
  width: 100%;
  height: 22px;
  line-height: 22px;
  display: inline-block;
  padding: 0 10px;
  margin: 0;
  vertical-align: middle;
}
.high-light-block .block-body .code-block .code-content .line-code:hover {
  background: #323232;
}
.high-light-block .block-body .code-block .code-content .line-code span:hover {
  background: #323232;
}
.high-light-block .block-body .code-block .code-content span:hover {
  background: #323232;
}
.high-light-block .block-body .code-block .code-content span::-moz-selection {
  background: #0D6AD9;
}
.high-light-block .block-body .code-block .code-content span::selection {
  background: #0D6AD9;
}
.high-light-block .block-body .code-block::-moz-selection {
  background: #0D6AD9;
}
.high-light-block .block-body .code-block::selection {
  background: #0D6AD9;
}
.high-light-block .block-body .code-block.nodata {
  color: #f90;
  text-align: center;
}
.high-light-block pre,
.high-light-block code {
  line-height: 1.5;
  display: block;
  background: #3f3f3f;
  border: none;
}
.high-light-block pre {
  margin: 0;
}

@font-face {font-family: "iconfont";
  src: url(data:application/vnd.ms-fontobject;base64,); /* IE9*/
  src: url(data:application/vnd.ms-fontobject;base64,#iefix) format('embedded-opentype'), 
  url('data:application/x-font-woff;charset=utf-8;base64,') format('woff'),
  url(data:application/x-font-ttf;base64,) format('truetype'), 
  url(data:image/svg+xml;base64,#iconfont) format('svg'); /* iOS 4.1- */
}

.iconfont {
  font-family:"iconfont" !important;
  font-size:16px;
  font-style:normal;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

